.NumberChip {
  display: inline-block;
  width: 50px;
  height: 50px;
  margin: 5px;
  border-radius: 50%;
  background-color: #606060;
  color: #FFFFFF;
  font-size: 32px;
  text-align: center;
}

.NumberChip.in-use {
  opacity: .5;
}

.NumberChip.used {
  background-color: #009688;
}



